* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

header {
  height: 80px;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  display: flex;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .1);
}

.filter {
  align-items: center;
  display: flex;
}

.filter label {
  margin-right: 10px;
}

.filter select {
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
}

input[type="text"] {
  width: 300px;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
}

main {
  max-width: 1200px;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  margin: 0 auto;
  padding: 20px;
  display: grid;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.country-card {
  width: 100%;
  height: 0;
  background-color: #fff;
  border-radius: 5px;
  padding-bottom: 56.25%;
  position: relative;
  overflow: hidden;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .1);
}

.country-card img {
  width: 600px;
  height: 600px;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
}

